<template>
  <div class="hello">
    这里是B页面<br/>
    <Headers :title-txt="showTitleTxt"></Headers>
    111111111111111111111111111111111111  这里是接口错误的http请求处理 这里是B页面<br/>
    <button @click="box()">提交</button>
    这里是接口错误的http请求处理
    111111111111111111111111111111111111  这里是接口错误的http请求处理 这里是B页面<br/>
    <button  @click="box()">提交</button>
    这里是接口错误的http请求处理
    111111111111111111111111111111111111
<div id="box" ref="box">11111</div>
    <router-link to="/list/A#bos">返回</router-link>
    <button @click="text">5555555555</button>
    这里是接口错误的http请求处理 这里是B页面<br/>
    <button @click="box()">提交</button><button @click="text">5555555555</button>
    这里是接口错误的http请求处理 这里是B页面<br/>
    <el-tooltip v-if="totop" class="item" effect="dark" content="置顶吸顶" placement="top">
      <el-button  @click="toTop"  style="position: fixed;bottom: 100px;right: 30px;background: red">
        <i class="el-icon-arrow-up"></i>
      </el-button>
    </el-tooltip>
    <el-tooltip v-if="totop" class="item" effect="dark" content="置底" placement="bottom">
      <el-button  @click="toTop"  style="position: fixed;bottom: 60px;right: 30px;background: red">
        <i class="el-icon-arrow-down"></i>
      </el-button>
    </el-tooltip>
  </div>
</template>

<script>
  import Headers from '../components/component/headers'
  export default {
    name: 'HelloWorld',
    data() {
      return {
        msg: 'Welcome to Your Vue.js App',
        showTitleTxt: {
          name: '首页'
        },
        totop:true
      }
    },
    components:{
      Headers
    },
    mounted () {
      window.addEventListener('scroll', this.handleScroll)
    },
    created() {

    },
    methods:{
      box(){
        this.axios.post("api/box", {})
          .then(response => {
            console.log(response.data.status)
          }, response => {
            console.log("error");
          })

      },
      handleScroll () {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
        if (scrollTop > 300) {
          this.totop = true
        } else {
          this.totop = false
        }
      },
      toTop(){
        let distance = document.documentElement.scrollTop || document.body.scrollTop; //获得当前高度
        console.log(distance)
        let step = distance/50; //每步的距离
        (function jump(){
          if(distance > 0){
            distance-=step;
            window.scrollTo(0,distance);
            setTimeout(jump,10)
          }
        })();
      },
      text(){
        history.back();
      },
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>




