**子组件代码   与b页面父子通信**
<template>
  <div @click="changeTitleTxt">
    <p @click="box">这是子组件代码</p>
    {{thisTitleTxt}}
  </div>
</template>
<script>
  export default {
    props: {
      titleTxt: Object
    },
    data () {
      return {
        thisTitleTxt: this.titleTxt
      }
    },
    methods:{
      box(){
        alert(1)
      },
      changeTitleTxt () {
        this.titleTxt.name = '切换'
      }
    }
  }
</script>



